@import url("https://fonts.googleapis.com/css2?family=Sora&display=swap");

* {
  font-family: "Sora", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Open Sans", sans-serif;
  background-color: #fff;
  color: #747474;
  font-size: 14px;
  line-height: 1.42857143;
  font-family: "Sora", sans-serif;
}
.sectionFrist {
  width: 100%;
  padding-top: 5px;
  display: flex;
  align-items: center;
  text-decoration: none;
  height: 100%;
}
.section2font {
  display: flex;
  width: 70%;
  padding-left: 5%;
  padding-top: 2%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.sectionpra {
  display: flex;
  padding-top: 8px;
}
.section2image {
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 25%;
  height: 100%;
}
.where {
  background-color: rgb(160, 68, 114);
  align-items: center;
  padding-left: 5%;
  padding-top: 2%;
  padding-bottom: 2%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: wheat;
}
.sectionsecondimage {
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
}
